
.newPost {
  display: flex;
  flex-direction: column;
  margin: auto 100px;

  .news {
    background: #d8d8d8;
    height: 80px;
    text-align: left;
    padding: 20px 0 0 20px;
    border-radius: 20px;
    box-shadow: 0px 3px 15px #1a1a1a;
    outline: none;
    border: none; }

  .buttonSend {
    align-self: flex-end;
    background: #c08403;
    width: 80px;
    border: none;
    outline: none;
    border-radius: 10px;
    text-align: center;
    margin-top: 20px;
    box-shadow: 0px 3px 10px #1a1a1a;
    transition: all .3s;
    padding: 7px;
    cursor: pointer;

    &:hover {
      box-shadow: 0px 2px 5px #1a1a1a;
      background: #de9304; } }

  .buttonSend:disabled {
    box-shadow: 0 2px 5px #1a1a1a;
    background: #969696; } }
