
.circle {
  background: #5cd1f5;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px; }

.post {
  display: flex;
  margin: 20px;
  padding: 10px 0 0 10px;
  background: #f9ffbb;
  box-shadow: 0 3px 15px #1a1a1a;
  border-radius: 20px;
  max-width: 400px;
  height: 80px; }

.like {
  justify-self: center;
  margin: 20px;
  color: #969696;

  img {
    width: 20px;
    padding-right: 5px;
    cursor: pointer; } }
